@use "global" as *;

.contact {
  margin-top: rem(85);
  @include mq(md) {
    margin-top: rem(115);
  }
}

.contact__inner {
  @include mq(md) {
    max-width: rem(746);
    width: 100%;
    margin: auto;
    padding: 0 $padding-pc;
  }
}

.contact__item {
  @include mq(md) {
    display: flex;
  }
}
.contact__item + .contact__item {
  margin-top: rem(32);
  @include mq(md) {
    margin-top: rem(45);
  }
}
.contact__label {
  font-size: rem(14);
  color: $white;
  @include mq(md) {
    text-align: right;
    max-width: rem(132);
    width: 100%;
    margin-top: rem(7);
  }
}

.contact__input {
  font-size: rem(14);
  font-weight: $regular;
  background: $white;
  width: 100%;
  padding: rem(10) 0 rem(10) rem(9);
  margin-top: rem(16);
  @include mq(md) {
    margin-left: rem(24);
    margin-top: 0;
    max-width: rem(540);
    padding: rem(7) 0 rem(7) rem(16);
  }
}

::placeholder {
  color: #979797;
}

.contact__btn {
  margin-top: rem(48);
  text-align: center;
  @include mq(md) {
    margin-top: rem(56);
    margin-left: auto;
    max-width: rem(540);
    width: 100%;
  }
}

.contact__submit {
  color: #000;
  background: $white;
  font-size: rem(20);
  letter-spacing: 2px;
  padding: rem(10) 0;
  text-align: center;
  width: 100%;
  @include mq(md) {
    max-width: rem(360);
    font-size: rem(16);
    padding: rem(18) 0;
  }
}

// input:invalid {
//   background: #FFDCDC;
// }

.contact__error-message {
  font-size: rem(16);
  color: #FF5757;
  letter-spacing: 2px;
  display: none;
}

